body {
  padding-top: 10rem;
  max-width: 100vw;
  min-height: 100vh;
  background-color: rgb(243, 247, 249);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@tailwind base;
@tailwind components;
@tailwind utilities;


button {
  background-color: #416466;
  color: white;
  padding: 0.6rem;
  border-radius: 1rem;
  width: 6rem;
  border-style: solid;
  border-color: white;
  border-width: 4px;
  box-shadow: 7px -4px 5px 0px rgba(0, 0, 0, 0.75) inset;
  -webkit-box-shadow: 7px -4px 5px 0px rgba(0, 0, 0, 0.75) inset;
  -moz-box-shadow: 7px -4px 5px 0px rgba(0, 0, 0, 0.75) inset;
}

button:active {

  box-shadow: 7px 7px 5px 0px rgba(0, 0, 0, 0.75) inset;
  -webkit-box-shadow: 7px 7px 5px 0px rgba(0, 0, 0, 0.75) inset;
  -moz-box-shadow: 7px 7px 5px 0px rgba(0, 0, 0, 0.75) inset;
}

#definitions {
  padding: 1rem;
  border-radius: 0 1rem;
 
  height: 20rem;
  color: rgb(10, 0, 0);
  background-color: #74f0f7;
}
#defCard{
box-shadow: -10px 13px 10px -2px rgba(133, 130, 130, 0.75), -18px 17px 5px -9px rgba(0, 0, 0, 0.11) inset;
}
#warning {
  display: flex;
  justify-content: center;
  text-align: center;
  padding: 2rem;
  color: red;
}

#defs {
  height: 30rem;
}

.header {
  background-color: #236366;
  box-shadow: 4px 3px 10px 5px rgba(168, 165, 165, 0.75) ;
}

#partOfSpeech {
  background-color: #1299a0;
  color: white;
  border-radius: 1rem 1rem 0rem 0rem;
}